        font-family: "Roboto-Regular";
        display: flex;
        flex-direction: column;
        &-navbar {
            height: 170px;
            padding-left: 21px;
            padding-right: 21px;
            background: rgba(44, 125, 250, 1);
            border-radius: 0px 0px 0px 25px;
            position: relative;
            &-title {
                margin-top: 38px;
                text-align: center;
                font-style: normal;
                font-weight: bold;
