.switch {
    position: relative;
    margin: 20px 0;
    &:before {
        content: "  ";
        position: absolute;
        left: 0;
        z-index: -1;
        width: 100%;
        height: 30px;
        background: rgb(234, 242, 255);
        border-radius: 25px;
    }
    &__label {
        font-size: 13px;
        padding: 8px 0;
        display: inline-block;
        width: 50%;
        text-align: center;
        cursor: pointer;
        transition: color 300ms ease-out;
        text-transform: capitalize;
    }
    &__indicator {
        width: 50%;
        height: 30px;
        position: absolute;
        top: 0;
        right: 0;
        border-radius: 25px;
        background: rgb(44, 125, 250);
        z-index: -1;
        transition: transform 800ms cubic-bezier(0.02, 0.94, 0.09, 0.97);
        transform: translate3d(0, 0, 0);
    }

    .switch__labelcolwhite {
        color: white;
    }

    input#one:checked ~ .switch__indicator {
        background: rgb(44, 125, 250);
        transform: translate3d(-100%, 0, 0);
    }
    input#two:checked ~ .switch__indicator {
        background: rgb(44, 125, 250);
        transform: translate3d(0%, 0, 0);
    }

    input#onetwo:checked ~ .switch__indicator {
        background: rgb(44, 125, 250);
        transform: translate3d(-100%, 0, 0);
    }
    input#twotwo:checked ~ .switch__indicator {
        background: rgb(44, 125, 250);
        transform: translate3d(0%, 0, 0);
    }

    input[type="radio"] {
        &:not(:checked),
        &:checked {
            display: none;
            color: black;
        }
    }
}

@media screen and (max-width: 320px) {
