$spinkit-size: 20px !default;
$spinkit-spinner-color: #2c7dfa !default;

.sk-rotating-plane {
    margin: 5px;
    margin-top: 10px;
    margin-left: 20px;
    width: $spinkit-size;
    height: $spinkit-size;
    background-color: $spinkit-spinner-color;
    animation: sk-rotating-plane 1.2s infinite ease-in-out;
}

@keyframes sk-rotating-plane {
    0% {
        transform: perspective(120px) rotateX(0deg) rotateY(0deg);
    }
    50% {
        transform: perspective(120px) rotateX(-180.1deg) rotateY(0deg);
    }
    100% {
        transform: perspective(120px) rotateX(-180deg) rotateY(-179.9deg);
    }
}

.sk-fading-circle {
    $circleCount: 12;
    $animationDuration: 1.2s;

    width: 2em;
    height: 2em;
    position: relative;
    margin: auto;

    .sk-circle {
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
    }

    .sk-circle:before {
        content: "";
        display: block;
        margin: 0 auto;
        width: 15%;
        height: 15%;
        background-color: $spinkit-spinner-color;
        border-radius: 100%;
        animation: sk-fading-circle-delay $animationDuration infinite ease-in-out both;
    }

    @for $i from 2 through $circleCount {
        .sk-circle-#{$i} {
            transform: rotate((360deg / $circleCount * ($i - 1)));
        }
    }

    @for $i from 2 through $circleCount {
        .sk-circle-#{$i}:before {
            animation-delay: (-$animationDuration + $animationDuration / $circleCount * ($i - 1));
        }
    }
}

@keyframes sk-fading-circle-delay {
    0%,
    39%,
    100% {
        opacity: 0;
    }
    40% {
        opacity: 1;
    }
}

/////////histori
.slick-track {
    position: relative;
    left: 0;
    top: 0;
    display: block;
    margin-left: auto;
    margin-right: auto;

    &:before,
    &:after {
        content: "";
        display: table;
    }

    &:after {
        clear: both;
    }

    .slick-loading & {
        visibility: hidden;
    }
}



.slick-slide {
    float: left;
    height: 100%;
    min-height: 1px;
    & > div {
        width: 80%;
        margin: 0 auto;
    }
    [dir="rtl"] & {
        float: right;
    }
    img {
        display: block;
    }
    &.slick-loading img {
        display: none;
    }

    display: none;

    &.dragging img {
        pointer-events: none;
    }

    .slick-initialized & {
        display: block;
    }

    .slick-loading & {
        visibility: hidden;
    }

    .slick-vertical & {
        display: block;
        height: auto;
        border: 1px solid transparent;
    }
}
.slick-arrow.slick-hidden {
    display: none;
}

.histori__header {
    overflow-x: hidden;
    background: #F6F8FE;
    & .slick-slider {
        width: 70%;
        margin: 0 auto;
    }
    &-phone {
        padding: 20px 0 12px 30px;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 19px;
        color: #adb5bd;
    }
    &-slide {
        height: 126px;
        background: #ffffff;
        box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.12);
        border-radius: 10px;
        position: relative;
        display: flex !important;
        align-items: center;
        justify-content: center;
    }
    &-titleslide {
        position: absolute;
        top: 20px;
        left: 30px;
        font-style: normal;
        font-weight: normal;
        font-size: 12px;
        line-height: 14px;
        color: #6c757d;
    }
    &-buttons {
        display: flex;
        justify-content: center;
        padding: 30px 0 20px;
    }
    &-ratio {
        width: 8px;
        height: 8px;
        background: rgba(0, 0, 0, 0.1);
        margin: 0px 8px;
        border-radius: 50%;
        &_active {
            background: rgba(0, 0, 0, 0.3);
        }
    }
}


.histori__graps{
    position: relative;
    width: 250px;
    height: 250px;
    margin: auto;
    display: flex;
    align-items: center;
    justify-content: center;
